// Typography
$font-size-base: 14px;
$line-height-base: 1.6;
$fonte-projeto-regular: 'GTWalsheimProRegular';
$fonte-projeto-bold: 'GTWalsheimProBold';
$fonte-admin-light: 'SF Pro Text Light';
//$fonte-admin-light: 'HelveticaNeueLTStd45Light';
$fonte-admin-medium: 'HelveticaNeueLTStd65Medium';

%transition_fast,
%transition_slow {
    text-decoration: none;
    transition-property: all;
    -webkit-transition-timing-function: cubic-bezier(0.165, 0.63, 0.14, 0.82);
    -moz-transition-timing-function: cubic-bezier(0.165, 0.63, 0.14, 0.82);
    -ms-transition-timing-function: cubic-bezier(0.165, 0.63, 0.14, 0.82);
    -o-transition-timing-function: cubic-bezier(0.165, 0.63, 0.14, 0.82);
    transition-timing-function: cubic-bezier(0.165, 0.63, 0.14, 0.82);
    //-webkit-font-smoothing: antialiased;
}

%transition_fast { transition-duration: 0.18s; }
%transition_slow { transition-duration: 1s; }

@font-face {
    font-family: 'GTWalsheimProRegular';
    src: url('../fonts/GTWalsheimPro/GTWalsheimProRegular.eot');
    src: url('../fonts/GTWalsheimPro/GTWalsheimProRegular.eot?#iefix') format('embedded-opentype'),
        url('../fonts/GTWalsheimPro/GTWalsheimProRegular.woff2') format('woff2'),
        url('../fonts/GTWalsheimPro/GTWalsheimProRegular.woff') format('woff'),
        url('../fonts/GTWalsheimPro/GTWalsheimProRegular.ttf') format('truetype'),
        url('../fonts/GTWalsheimPro/GTWalsheimProRegular.svg#GTWalsheimProRegular') format('svg');
    font-weight: normal;
    font-style: normal;
}

